import {Injectable} from '@angular/core';
import {CardFromApi} from '../model/CardFromApi';
import {DecklistCardPile} from '../model/DecklistCardPile';
import {observable, Observable, Subject, Subscriber} from 'rxjs';
import {NgxIndexedDB} from 'ngx-indexed-db';

@Injectable({
    providedIn: 'root'
})
export class ListStorageService {
    realCards: Map<string, DecklistCardPile>;
    readonly subject;
    private db;

    constructor() {
        this.realCards = new Map();
        this.subject = new Subject<Map<string, DecklistCardPile>>();
        this.db = new NgxIndexedDB('cardLists', 1);
        this.initDB();
    }

    public getCards(): Subject<Map<string, DecklistCardPile>> {
        return this.subject;
        this.initList();
    }

    public addCard(card: CardFromApi) {
        if (this.realCards.has(card.id)) {
            this.addCardToHashmap(card);
        } else {
            this.realCards.set(card.id, new DecklistCardPile(card, 1));
        }
        this.subject.next(this.realCards);
        this.saveList();
    }

    private addCardToHashmap(card: CardFromApi) {
        const cardPileCount = this.realCards.get(card.id).count + 1;
        this.realCards.get(card.id).count = cardPileCount;
    }

    public removeCard(card: CardFromApi) {
        if (this.realCards.has(card.id)) {
            this.removeCardFromHashmap(card);

            this.subject.next(this.realCards);
            this.saveList();
        }
    }

    private removeCardFromHashmap(card: CardFromApi) {
        const cardPileCount = this.realCards.get(card.id).count - 1;
        if (cardPileCount === 0) {
            this.realCards.delete(card.id);
            return;
        }
        this.realCards.get(card.id).count = cardPileCount;
    }

    private saveList() {


        this.db.update('decklist', {id: 1, key: 'decklist', list: this.mapToJson(this.realCards)}).then(
            () => {
            },
            error => {
                console.error(error);
            }
        );
    }

    private initDB() {
        this.db.openDatabase(5, evt => {
            const objectStore = evt.currentTarget.result.createObjectStore('decklist', {keyPath: 'id', autoIncrement: true});

            objectStore.createIndex('key', 'key', {unique: false});
            objectStore.createIndex('list', 'list', {unique: true});
        }).then(() => this.initList());
    }

    private initList() {
        this.db.getByKey('decklist', 1).then(
            list => {
                const cardMap = this.jsonToMap(list.list as Map<string, DecklistCardPile>) as Map<string, DecklistCardPile>;
                this.realCards = cardMap;
                this.subject.next(cardMap);
            },
            error => {
                console.error(error);
            }
        );
    }

    private mapToJson(map) {
        return JSON.stringify([...map]);
    }

    private jsonToMap(jsonStr) {
        return new Map(JSON.parse(jsonStr));
    }


}
